.file {
  width: 100%;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.file + label {
    font-size: 1.15em;
    font-weight: 700;
    color: white;
    background: steelblue;
    background-image: url('https://image.flaticon.com/icons/svg/1462/1462556.svg');
    background-size: 80px;
